<template>
  <div class="px-3 py-2">
    <div class="d-flex justify-content-between align-items-center">
      <div class="d-flex align-items-center">
        <img src="@/assets/images/logo/logo.png" alt="logo" width="80" />
        <span class="title-logo"> Smartwork </span>
      </div>

      <b-link
        to="/performance"
        class="d-flex align-items-center bg-white p-1 rounded-pill cursor-pointer"
      >
        <feather-icon icon="ArrowLeftIcon" />
        <span class="bold ml-1">Close</span>
      </b-link>
    </div>

    <div class="text-center mt-3">
      <h2 style="color: #1e1e1e">Setup Goals</h2>
      <span class="text-muted">
        Please fill in the form below to setup your goals
      </span>
    </div>

    <b-card class="mt-3 rounded-3">
      <form>
        <b-row
          v-for="(item, index) in items"
          :id="item.id"
          :key="item.id"
          ref="row"
        >
          <b-col md="4">
            <b-form-group label="Strategic Target">
              <b-form-input
                v-model="strategy[index]"
                placeholder="Strategic Target"
                required
                class="rounded-pill"
              ></b-form-input>
            </b-form-group>
            <b-form-group label="Weight">
              <b-form-input
                v-model="weight[index]"
                placeholder="Weight"
                required
                class="rounded-pill"
              ></b-form-input>
            </b-form-group>
            <b-form-group label="Target">
              <b-form-input
                v-model="target[index]"
                placeholder="Target"
                required
                class="rounded-pill"
              ></b-form-input>
            </b-form-group>
            <b-form-group label="Due Date">
              <flat-pickr
                v-model="due_date[index]"
                placeholder="Due Date"
                class="rounded-pill"
                :config="{
                  altInput: true,
                  altFormat: 'F j, Y',
                  dateFormat: 'Y-m-d',
                }"
              />
            </b-form-group>
          </b-col>
          <b-col md="8">
            <b-form-group label="Key Performance Indicator">
              <b-form-textarea
                v-model="kpi[index]"
                placeholder="Key Performance Indicator"
                required
                style="min-height: 277px; max-height: 277px"
              ></b-form-textarea>
            </b-form-group>
          </b-col>
          <b-col cols="12">
            <div class="text-right">
              <b-button
                type="button"
                variant="outline-danger"
                class="rounded-3 mr-1"
                @click="remove(index)"
                size="sm"
                v-if="index > 0"
              >
                Remove
              </b-button>
            </div>
          </b-col>
        </b-row>
      </form>
    </b-card>
    <div class="text-right">
      <b-button
        type="button"
        variant="outline-primary"
        class="rounded-3 mr-1"
        @click="repeate"
      >
        Add Goal
      </b-button>
      <b-button
        type="submit"
        variant="primary"
        class="rounded-3"
        @click="submit_goals($event)"
      >
        Continue
      </b-button>
    </div>
  </div>
</template>

<style>
.title-logo {
  font-size: 1.5rem;
  font-weight: 600;
  color: #423ffd;
  margin-left: 0.5rem;
}

.rounded-3 {
  border-radius: 1.75rem;
}
</style>

<script>
export default {
  data() {
    return {
      strategy: [""],
      weight: [""],
      target: [""],
      due_date: [""],
      kpi: [""],

      //
      items: [{}],
      nextTodoId: 2,
    };
  },

  mounted() {
    const goals = localStorage.getItem("sw_goals")
      ? JSON.parse(localStorage.getItem("sw_goals"))
      : [];

    if (goals.length > 0) {
      goals.map((goal) => {
        this.strategy = goal.strategy;
        this.weight = goal.weight;
        this.target = goal.target;
        this.due_date = goal.due_date;
        this.kpi = goal.kpi;
      });

      this.items.push({
        id: (this.nextTodoId += this.nextTodoId),
      });
    }
  },

  methods: {
    repeate() {
      this.items.push({
        id: (this.nextTodoId += this.nextTodoId),
      });
    },

    remove(index) {
      this.items.splice(index, 1);
      this.strategy.splice(index, 1);
      this.weight.splice(index, 1);
      this.target.splice(index, 1);
      this.due_date.splice(index, 1);
      this.kpi.splice(index, 1);
    },

    submit_goals(e) {
      e.preventDefault();
      // save to local storage with type array of object
      let goals = JSON.parse(localStorage.getItem("sw_goals")) || [];
      goals.push({
        strategy: this.strategy,
        weight: this.weight,
        target: this.target,
        due_date: this.due_date,
        kpi: this.kpi,
      });
      localStorage.setItem("sw_goals", JSON.stringify(goals));

      this.$router.push({
        name: "performance/goal/summary",
      });
    },
  },
};
</script>